body {
  margin: 0;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 12px;
  --color-menu-panel: #00acdc;
}

.head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  box-shadow: 0 2px 4px rgb(0, 0, 0, 0.05);
}

.logo {
  display: block;
  height: 40px;
  width: 40px;
}

.nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  display: flex;
  text-transform: uppercase;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-decoration: none;
  color: #000;
  position: relative;
  line-height: 60px;
}

.nav__link:hover {
  color: var(--color-menu-panel);
  transition: color 0.3s;
}

.is-active {
  color: var(--color-menu-panel);
}

.is-active::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 2px;
  background-color: var(--color-menu-panel);
}
